:root{
    --mainGreen: rgb(103, 175, 103);
    --darkGreen: #488d36;
    --notActive: #efefef;
    --tableHover: red;
}
textarea:focus, input:focus, button:focus{
    outline: none;
}
.nav-input{
    width: 13rem;
    height: 40px;
    padding-left: 15px;
    font: inherit;
    padding-bottom: 2px;
    margin-top: 15px;
    border: 1px solid gray;
    box-sizing: border-box;
    cursor: pointer;
}
